// React/routing dependencies
var React           = require('react');
var Router          = require('react-router');
var Route           = Router.Route;
var Routes          = Router.Routes;
var NotFoundRoute   = Router.NotFoundRoute;
var DefaultRoute    = Router.DefaultRoute;
var Link            = Router.Link;

// Components
var HelperQueue     = require('./HelperQueue.jsx');
var SignupPage      = require('./SignupPage.jsx');
var NotFound        = require('./NotFound.jsx');

var App = React.createClass({displayName: 'App',
    // TODO: don't expose links to go between each view; password protect
    render: function() {
        return (
            React.createElement("div", {className: "container-fluid"}, 
                React.createElement(this.props.activeRouteHandler, null)
            )
        );
    }
});

var routes = (
    React.createElement(Routes, {location: "history"}, 
        React.createElement(Route, {name: "app", path: "/", handler: App}, 
            React.createElement(Route, {name: "queue", handler: HelperQueue}), 
            React.createElement(Route, {name: "signup", handler: SignupPage}), 
            React.createElement(DefaultRoute, {handler: NotFound})
        ), 
        React.createElement(NotFoundRoute, {handler: NotFound})
    )
);

React.renderComponent(routes, document.body);
